/**
 * 检测测试数据是否为 Window 对象
 * ========================================================================
 * @method isWindow
 * @since 2.7.0
 * @category DOM
 * @param {*} val - 要检测的数据
 * @return {Boolean} 'val' 是 Window 对象，返回 true，否则返回 false
 */
const isWindow = (val) => {
  return val && val.document && val.location && val.alert && val.setInterval
}

export default isWindow
